<template>
    <div id="Alert">
      <div v-show="alert.status" :class="alert.class" class="fixed z-50 w-full flex justify-center alertFade" style="top:10%">
        <span :class="alert.color" class="px-10 py-2 rounded-lg font-bold text-black text-3xl" v-text="alert.msg" />
      </div>
    </div>
</template>

<script>
export default {
  name: 'Alrt',
  props:{},
  data() {
      return {
        alert:{status:false,class:"opacity-0",color:null,msg:null},
        alerttimeout1:null,
        alerttimeout2:null,
      }
  },
  methods: {
    alertShow(msg,color = "bg-green-500",Time = 500){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = color;
      vm.alert.msg = msg;

      vm.alertFadeOut(Time);
    },
    alertTimeOver(){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = "bg-red-500";
      vm.alert.msg = "Time Over :(";

      vm.alertFadeOut(7000);
    },
    alertFadeOut(sec){
      clearTimeout(this.alerttimeout1)
      clearTimeout(this.alerttimeout2)
      var vm = this;
      vm.alerttimeout1 = setTimeout(function(){
        vm.alert.class = "opacity-0"
        vm.alerttimeout2 = setTimeout(function() {
          vm.alert.status = false;
        }, 500);
      },sec);
    },


    dataForApp(){
      this.$emit('dataForApp', this.level);
    }
  },
  watch: {
    
  }
}
</script>
<style scoped>
.alertFade{
  /*transition: all;*/
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
}
</style>