<template>
  <div id="WhatIsTheSing" class="pb-16">
    
    <Alert ref="Alert"/>

    
    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :score="score" :maxW="'max-w-xl'" ref="InteractiveBord"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">

        <div class="h-full w-full py-10 rounded-lg text-4xl overflow-hidden border bg-purple-600 shadow-lg" >
          <span v-text="questionPart1" />
          <span v-text="' .... '" />
          <span v-text="questionPart2" />
          <span v-text="' = '" />
          <span v-text="answer" />
        </div>

        <div class="w-full mt-10 flex flex-wrap justify-center">
          
          <div class="w-full px-10 flex flex-wrap">
            <div class="w-1/2 p-2" v-for="sing in mathEx" :key="sing">
              <div class="py-4 text-5xl md:text-6xl bg-purple-500 rounded-lg cursor-pointer" @click="clickSing(sing)" v-text="sing == '*' ? 'X':sing" />
            </div>
          </div>

        </div>
      </div>
    </div>
    
    <LevelsSelect @dataForApp="updateLevelValue" />
    
  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'WhatIsTheSing',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      questionPart1:null,
      questionPart2:null,
      sing:null,
      answer:null,

      mathEx:["+","-","*","/"],

      score:0,

      stopGame:false,

      level:0,
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.getQuestion();
    },
    clickSing(sing){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();

      var alert = vm.$refs.Alert;
      if(vm.sing == sing){
        vm.score += 2;
        alert.alertShow("+2")
        console.log("+2")
      }else {
        if(vm.score > 0)vm.score -= 1;
        if(vm.score > 0)alert.alertShow("-1","bg-red-500")
        else alert.alertShow("0","bg-red-500")
        console.log("-1")
      }

      this.getQuestion();
    },
    getQuestion(){
      var vm=this;
      var num1=0,num2=0,sing,answerPR;
      var mathE = vm.mathEx[Math.floor(Math.random() * vm.mathEx.length)];

      if (mathE == "+") {
        num1 = Math.floor(Math.random() * vm.getRandomRange());
        num2 = Math.floor(Math.random() * vm.getRandomRange());
        
        sing = "+"
        answerPR = num1 + num2
      }else if (mathE == "-") {
        do {
          num1 = Math.floor(Math.random() * vm.getRandomRange());
          num2 = Math.floor(Math.random() * vm.getRandomRange());
        } while (num1 < num2);
        
        sing = "-"
        answerPR = num1 - num2
      }else if (mathE == "*") {
        num1 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        num2 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        
        sing = "*"
        answerPR = num1 * num2
      }else if (mathE == "/") {
        do {
          num1 = Math.floor(Math.random() * (vm.getRandomRange() * 2));
          num2 = Math.floor(Math.random() * (vm.getRandomRange() / 2));
        } while (num1 < num2 || num2 == 0 || num1%num2!=0);
        
        sing = "/"
        answerPR = num1 / num2
      }

      vm.questionPart1 = num1
      vm.questionPart2 = num2
      vm.sing = sing
      vm.answer = answerPR
    },

    getRandomRange(){
      var range = 30;
      if(this.level == 0)range = 40;
      else if(this.level == 1)range = 80;
      else if(this.level == 2)range = 120;

      return range;
    },


    updateTimeOver(){
      this.stopGame = true;
      this.$refs.Alert.alertTimeOver();

    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
}
</script>

