<template>
    <div id="InteractiveBord">
      <div class="w-full flex flex-col items-center mt-16 font-bold text-md lg:text-xl">
        <div :class="maxW" class="w-full flex flex-wrap justify-center items-center">
          <div class="">
            <button class='bg-purple-500 rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="$emit('changeSetting')">Restart</button>
          </div>
        </div>
        <div :class="maxW" class="w-full flex flex-wrap justify-between mt-5 items-center">
          <div v-if="count" class="mx-10">Count : <span v-text="count" /></div>
          <div v-if="!count" class="mx-10">Score : <span v-text="score" /></div>
          <div v-if="haveTimeLeft" class="mx-10">Time Left : <span :class="getTimeColor()" v-text="timer"/> s</div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'InteractiveBord',
  props:{
    score:{
      type: Number,
      default: 0
    },
    count:{
      type: String,
      default: null
    },
    haveTimeLeft:{
      type:Boolean,
      default:true
    },
    timeLimit:{
      type: Number,
      default: 60
    },
    maxW:{
      type:String,
      default:"max-w-5xl"
    }
  },
  data() {
      return {
        timePassed: 0,
        timerInterval: null,

        stopGame:false
      }
  },
  methods: {
    getTimeColor(){
      var vm = this;
      if(vm.timePassed == vm.timeLimit)return 'text-red-500'
      else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
      else return 'text-green-500'
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(r=0){
      clearInterval(this.timerInterval);
      if(r)Object.assign(this.$data,this.$options.data.call(this))
    },
    TimeOver(){
      if (!this.stopGame) {
        this.stopGame = true;
        this.stopTimer();
        this.dataForApp();
        console.log("Time Over")
      }
    },


    dataForApp(){
      this.$emit('dataForApp');
    }
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.TimeOver();
      return timeLeft
    },
  },
  watch: {
    /*level(){
      this.dataForApp();
    }*/
  }
}
</script>